@import url(https://fonts.googleapis.com/css2?family=Work+Sans:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=PT+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.App {
  font-family: 'Work Sans', sans-serif;}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


.main_dashboard{
    display: flex;
    align-items: center;
    justify-content: flex-start;
    padding: 4px 12px 4px 13px;
    height: 32px;
    width: 100%;
    background: #D0EEFA;
    border-radius: 6px;
    cursor: pointer;
}
.main_dashboard p{
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 24px;
    letter-spacing: -0.006em;
    color: #24AEE4;
    flex: none;
    order: 1;
    flex-grow: 1;
    margin: 0 8px;
}
.other-dashboardItems{
    display: flex;
    align-items: center;
    justify-content: flex-start;
    height: 32px;
    padding: 0 13px;
    width: 100%;
    border-radius: 6px;
    cursor: pointer;
}
.other-dashboardItems p{
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 24px;
    letter-spacing: -0.006em;
    color: #252C32;
    margin: 0 8px;
    display: flex;
    width: 165px;
    justify-content: space-between;
    align-items: center;

}
.other-dashboardItems span{
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 4px;
    width: 23px;
    height: 16px;
    background: #EB5757;
    border-radius: 4px;
    font-style: normal;
    font-weight: 600;
    font-size: 12px;
    line-height: 16px;
    color: #FFFFFF;
    flex: none;
    order: 0;
    flex-grow: 0;
    margin: 0 0;
}

.sub_menuText{
    font-style: normal;
    font-weight: normal;
    font-size: 13px;
    cursor: pointer;
    line-height: 100%;
    height: 33px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #000000;
    transition: .7s;
}
.sub_menuText p{
    display: flex;
    width: 151px;
    justify-content: space-between;
    align-items: center;
}
.sub_menuText span{
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 4px;
    width: 15px;
    height: 16px;
    background: #2D9CDB;
    border-radius: 4px;
    font-style: normal;
    font-weight: 600;
    font-size: 12px;
    line-height: 16px;
    color: #FFFFFF;
    flex: none;
    order: 0;
    flex-grow: 0;
    margin: 0 0;
}
.sub_menuText:hover{
    color: #ED2147;
}
.sub_menuText div{
    height: 1px;
    width: 15px;
    margin-right: 3px;
    background: #C4C4C4;
}
.PendingApplication_main{
    margin-top: 70px;
}

.pendingApplication_content{
    background: #F6F8F9;;
    padding: 30px;
}

.header_heading h2{
    margin-bottom: 5px;
    font-style: normal;
    font-weight: 600;
    font-size: 24px;
    line-height: 180%;
    color: #000000;

}
.headerLocation_div{
    display: flex;
}
.header_website{
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
.locationDiv{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-left: 30px;
}
.headerLocation_div p{
    margin-left: 7px;
}
.header_website p{
    font-style: normal;
    font-weight: normal;
    font-size: 10px;
    line-height: 180%;
    color: #000000;

}

.subLogo{
    display: flex;
    align-items: center;
    justify-content: flex-end;
}
.subLogo img{
    width: 148px;
    height: 42px;

}

@media (max-width: 600px) {
    .header_heading{
        display: flex;
        flex-direction: column;
        justify-content: center;
    }
    .headerLocation_div{
        display: flex;
        flex-direction: column;
        justify-content: center;
    }
    .header_website{
        display: flex;
        justify-content: center;
    }
    .header_heading p,h2{
        text-align: center;
    }
    .subLogo{
        justify-content: center;
    }
}
.Applications_main{
    margin-top: 50px;
}

.applicationContainer_heading h2{
    font-style: normal;
    font-weight: 500;
    font-size: 24px;
    line-height: 32px;
    letter-spacing: -0.019em;
    color: #A2A2A2;

}
.ApplicationsCard_main{
    height: auto;
    padding: 10px;
    background: #FFFFFF;
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.32);
    border-radius: 6px;
}
.userInfo_Div{
    display: flex;
    align-items: center;
    padding:10px 20px;
}
.userInfo_name{
    padding: 5px;
    margin-left: 10px;
}
.userInfo_name h2{
    margin: 0;
    font-style: normal;
    font-weight: bold;
    font-size: 16px;
    line-height: 180%;
    color: #000000;
}
.userInfo_name p{
    font-family: 'PT Sans', sans-serif;
    font-style: normal;
    font-weight: normal;
    margin: 0;
    font-size: 11px;
    line-height: 180%;
    /* or 20px */


    color: #000000;
}


.applicationCard_center{
    margin-top: 5px;
    display: flex;
}
.applicationCard_center_firstDiv{
    display: flex;

}

.applicationCard_center_general{
    display: flex;
    align-items: center;
}
.applicationCard_center_general p{
    margin: 0;
    font-style: normal;
    font-weight: normal;
    font-size: 10px;
    line-height: 180%;
}
.applicationCard_center_general img{
    margin: 0 10px;
}

.applicationCard_center_div2 div{
    margin-left: 20px;
}
.applicationCard_center_div3{
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding-right: 20px;
    flex: 1 1;
}
.applicationCard_center_div3 button{
    margin-left:10px ;
}
.divider{
    margin:20px;
    width: 70%;
}
.tags{
    display: flex;
    padding-left: 10px;
    padding-bottom: 10px;
}

.tags div{
    display: flex;
}
.tags p{
    height: 16px;
    font-style: normal;
    font-weight: 600;
    font-size: 12px;
    color: #333333;
    flex: none;
    order: 0;
    flex-grow: 0;
    margin: 0 10px;

    display: flex;
    align-items: center;
    padding: 0 4px;
    background: #E0E0E0;
    border-radius: 4px;
}

@media (max-width: 1200px){
    .applicationCard_center_firstDiv{
        display: flex;
        flex-direction: column;
    }
}

@media (max-width: 1020px) {

    .applicationCard_center{
        display: flex;
        flex-direction: column;
        /*align-items: center;*/
    }
    .userInfo_Div{
        display: flex;
    }
    .applicationCard_center_firstDiv{
        display: flex;
        flex-direction: column;
    }
    .applicationCard_center_general{
        margin-top: 20px;
    }
    .applicationCard_center_div2 div{
        margin-left: 0;
    }
    .applicationCard_center_div3{
        margin-top: 20px;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
    }
    .applicationCard_center_div3 button{
        margin-top: 10px;
        margin-left: 0;
    }
    .divider{
        width: 90%;
        margin: 20px;
    }
    .tags{
        display: flex;
        flex-direction: column;
        padding-left: 0;

    }
    .tags2{
        margin-top: 10px;
    }


}
